import "./navbar.scss";
import SearchIcon from "@mui/icons-material/Search";
import PersonOutlineIcon from "@mui/icons-material/PersonOutline";
import ShoppingCartOutlinedIcon from "@mui/icons-material/ShoppingCartOutlined";
import { Link } from "react-router-dom";
import Cart from "../cart/Cart";
import { useState } from "react";
import { useSelector } from "react-redux";

const Navbar = () => {
  const [open, setOpen] = useState(false);
  const products = useSelector((state) => state.cart.products);

  return (
    <div className="navbar">
      <div className="wrapper">
        <div className="left">
          <div className="item">
            <Link className="link">PRODUIT CHEVEUX</Link>
          </div>
          <div className="item">
            <Link className="link" to="./products/1">
              MATRIEL LECTRIQUE
            </Link>
          </div>
          <div className="item">
            <Link className="link">HOMMES</Link>
          </div>
          <div className="item">
            <Link className="link">MARQUES</Link>
          </div>
          <div className="item">
            <Link className="link">PROMOS</Link>
          </div>
        </div>
        <div className="center">
          <Link className="link">MON INSTANT</Link>
        </div>
        <div className="right">
          <div className="item">
            <Link className="link">Page d'accueil</Link>
          </div>
          <div className="item">
            <Link className="link">Contactez-nous</Link>
          </div>
          <div className="item">
            <Link className="link">FAQ</Link>
          </div>
          <div className="item">
            <Link className="link"> propos</Link>
          </div>

          <div className="icons">
            <SearchIcon />
            <PersonOutlineIcon />
            <div className="cartIcon">
              {/* if it's true, it's gonna be false - if it's false it's gonna be true */}
              <ShoppingCartOutlinedIcon onClick={() => setOpen(!open)} />
              <span>{products.length}</span>
            </div>
          </div>
        </div>
      </div>
      {open && <Cart />}
    </div>
  );
};

export default Navbar;
