import { Link } from "react-router-dom";
import "./card.scss";

const Card = ({ item }) => {
  return (
    <Link className="link" to={`/product/${item.id}`}>
      <div className="card">
        <div className="image">
          {item?.attributes.isNew && <span>Nouveau</span>}
          <img
            src={
              process.env.REACT_APP_UPLOAD_URL + item.attributes?.img1?.data?.attributes.url
            }
            alt=""
            className="img"
          />
        </div>
        <div className="stock">{item.inStock}</div>
        <h2>{item?.attributes.brand}</h2>
        <h3>{item?.attributes.title}</h3>
        <div className="price">
          <h2>{item?.attributes.price} </h2>
        </div>
        {/* <button className="add-to-cart btn btn-info">Ajouter au panier</button> */}
      </div>
    </Link>
  );
};

export default Card;
