import { useParams } from "react-router-dom";
import List from "../../components/list/List";
import "./products.scss";
import { useState } from "react";
import useFetch from "../../hooks/useFetch";

const Products = () => {
  //fetching data according to category id in url
  const catId = parseInt(useParams().id);

  //update maximal price when changing range input
  const [maxPrice, setMaxPrice] = useState(300);

  const [sort, setSort] = useState(null); //at the begenning we're not gonna sorting anything
  //there is a problem here 'null' in strapi sort asc and desc not null
  //better asc par default

  const { data } = useFetch(
    `/sub-categories?[filters][categories][id][$eq]=${catId}`
  );

  const [selectedSubCats, setSelectedSubCats] = useState([]);

  const handleChange = (e) => {
    const value = e.target.value;
    //because when we check an item we are going to add this subCat'id inside a list and when uncheck we should delete this from our list
    const isChecked = e.target.checked;

    setSelectedSubCats(
      isChecked
        ? [...selectedSubCats, value]
        : selectedSubCats.filter((item) => item !== value)
    );
  };

  return (
    <div className="products">
      <div className="left">
        <div className="filterItem">
          <h2>CATGORIES</h2>
          {data?.map((item) => (
            <div className="inputItem" key={item.id}>
              <input
                type="checkbox"
                id={item.id}
                value={item.id}
                onChange={handleChange}
              />
              <label htmlFor={item.id}>{item.attributes.title}</label>
            </div>
          ))}
        </div>
        <div className="filterItem">
          <h2>PRIX</h2>
          <div className="inputItem">
            <span>0</span>
            <input
              type="range"
              min={0}
              max={300}
              onChange={(e) => setMaxPrice(e.target.value)}
            />
            <span>{maxPrice}</span>
          </div>
        </div>
        <div className="filterItem">
          <h2>Trier par</h2>
          <div className="inputItem">
            <input
              type="radio"
              id="asc"
              value="asc"
              name="price"
              onChange={(e) => setSort("asc")}
            />
            <label htmlFor="asc">Le moins cher</label>
          </div>
          <div className="inputItem">
            <input
              type="radio"
              id="asc"
              value="desc"
              name="price"
              onChange={(e) => setSort("desc")}
            />
            <label htmlFor="asc">Le plus cher</label>
          </div>
        </div>
      </div>
      <div className="right">
        <img
          src="https://prohair.ma/wp-content/uploads/2020/03/BEAUTY-TECHNOLOGIE.png"
          className="catImg"
          alt=""
        />
        <List
          catId={catId}
          maxPrice={maxPrice}
          sort={sort}
          subCats={selectedSubCats}
        />
      </div>
    </div>
  );
};

export default Products;
